import React, { useState, useEffect, useCallback } from 'react';
import Snake from './Snake';
import Food from './Food';

// Функция для получения рандомных координат еды
const getRandomCoordinates = () => {
  let min = 1;
  let max = 98;
  let x = Math.floor((Math.random() * (max - min + 1) + min) / 2) * 2;
  let y = Math.floor((Math.random() * (max - min + 1) + min) / 2) * 2;
  return [x, y];
}

function App() {

  const [snakeDots, setSnakeDots] = useState([
    [0, 0],
    [2, 0]
  ]); // Координаты спавна змейки в начале игры
  const [snakeFood, setSnakeFood] = useState(getRandomCoordinates()); // Координаты спавна еды
  let [snakeSpeed, setSnakeSpeed] = useState(200); // Указываем скорость перемещения змейки
  const [direction, setDirection] = useState('RIGHT'); // Направление движение змейки в начале игры



  // Задаём направление движения для змейки
  let onKeyDown = (e) => {
    e = e || window.event;
    switch (e.keyCode) {
      case 38:
        setDirection('UP');
        break;
      case 40:
        setDirection('DOWN');
        break;
      case 37:
        setDirection('LEFT');
        break;
      case 39:
        setDirection('RIGHT');
        break;
    }
  }

  let moveSnake = useCallback(() => {
    let dots = [...snakeDots];
    let head = dots[dots.length - 1];
    switch (direction) {
      case "RIGHT":
        head = [head[0] + 2, head[1]];
        break;
      case "LEFT":
        head = [head[0] - 2, head[1]];
        break;
      case "DOWN":
        head = [head[0], head[1] + 2];
        break;
      case "UP":
        head = [head[0], head[1] - 2];
        break;
      default:
    }
    dots.push(head);
    dots.shift();
    setSnakeDots(dots);
  }, [snakeDots, direction]);

  useEffect(() => {
    let timer = setTimeout(moveSnake, snakeSpeed);
    document.onkeydown = onKeyDown;
    checkIfEat();
    checkIfOutOfCodrinats();
    checkIfCollapsed();
    return () => {
      clearTimeout(timer);
      document.onkeydown = null;
    };
  }, [moveSnake]);

  let checkIfOutOfCodrinats = () => {
    let head = snakeDots[snakeDots.length - 1];
    if (head[0] >= 100 || head[1] >= 100 || head[0] < 0 || head[1] < 0) {
      onGameOver();
    }
  }

  let checkIfCollapsed = () => {
    let snake = [...snakeDots];
    let head = snake[snake.length - 1];
    snake.pop();
    snake.forEach(dot => {
      if (head[0] === dot[0] && head[1] === dot[1]) {
        onGameOver();
      }
    })
  }

  let enlargeSnake = () => {
    let newSnake = [...snakeDots];
    newSnake.unshift([]);
    setSnakeDots(newSnake);
  }

  let checkIfEat = () => {
    let head = snakeDots[snakeDots.length - 1];

    if (head[0] === snakeFood[0] && head[1] === snakeFood[1]) {
      setSnakeFood(getRandomCoordinates());
      setSnakeSpeed(snakeSpeed -= 10);
      enlargeSnake();
    }
  }

  let onGameOver = () => {
    alert(`Game Over. Snake length is ${snakeDots.length}`);
    setSnakeDots([
      [0, 0],
      [2, 0]
    ]);
    setDirection('RIGHT');
    setSnakeSpeed(200);
  }

  return (
    <div className="game-area">
      <Snake snakeDots={snakeDots} />
      <Food dot={snakeFood} />
    </div>
  );
}

export default App;
